<script>
  export default {
    data() {
      return {
        students: [],
        studentClass:[],
        keywords: '',
        fileUploadBtnText: '导入数据',
        beginDateScope: '',
        faangledoubleup: 'fa-angle-double-up',
        faangledoubledown: 'fa-angle-double-down',
        dialogFormulaTitle: '',
        dialogOrdTitle:'',
        dialogTitle: '',
        multipleSelection: [],
        depTextColor: '#c0c4cc',
        totalCount: -1,
        currentPage: 1,
        deps: [],
        defaultProps: {
          label: 'name',
          isLeaf: 'leaf',
          children: 'children'
        },
        dialogFormulaVisible: false,
        dialogOrdVisible: false,
        dialogVisible: false,
        tableLoading: false,
        advanceSearchViewVisible: false,
        showOrHidePop: false,
        showOrHidePop2: false,
        formulas: {
          formula:'',
          flag:1
        },
        student: {
          name: '',
          studentId:'',
          gender: '',
          studentClassId: '',
          among_them:'',
          final_exam:'',
          flag:2,
          create_by:JSON.parse(window.localStorage.getItem("user")).id,
          update_by:JSON.parse(window.localStorage.getItem("user")).id
        },
        rules: {
          name: [{required: true, message: '必填:姓名', trigger: 'blur'}],
          studentId: [{required: true, message: '必填:学号', trigger: 'blur'}],
          gender: [{required: true, message: '必填:性别', trigger: 'blur'}],
          studentClassId: [{required: true, message: '必填:班级', trigger: 'blur'}],
/*          idCard: [{
            required: true,
            message: '必填:身份证号码',
            trigger: 'blur'
          }, {
            pattern: /(^[1-9]\d{5}(18|19|([23]\d))\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{3}[0-9Xx]$)|(^[1-9]\d{5}\d{2}((0[1-9])|(10|11|12))(([0-2][1-9])|10|20|30|31)\d{2}$)/,
            message: '身份证号码格式不正确',
            trigger: 'blur'
          }],*/
/*          email: [{required: true, message: '必填:电子邮箱', trigger: 'blur'}, {
            type: 'email',
            message: '邮箱格式不正确',
            trigger: 'blur'
          }]*/
        }
      };
    },
    mounted: function () {
      this.initData();
      this.loadEmps();
    },
    methods: {
      fileUploadSuccess(response, file, fileList){
        if (response) {
          this.$message({type: response.status, message: response.msg});
        }
        this.loadEmps();
        this.fileUploadBtnText = '导入数据';
      },
      fileUploadError(err, file, fileList){
        this.$message({type: 'error', message: "导入失败!"});
        this.fileUploadBtnText = '导入数据';
      },
      beforeFileUpload(file){
        this.fileUploadBtnText = '正在导入';
      },
      exportEmps(){
//        var iframe = document.createElement("iframe");
//        iframe.style.display = 'none';
//        iframe.src = "/employee/basic/exportEmp";
//        iframe.onload=function () {
//          document.body.removeChild(iframe);
//        }
//        document.body.appendChild(iframe);
        window.open("/student/mana/exportStudent", "_parent");
      },
      cancelSearch(){
        this.advanceSearchViewVisible = false;
        this.emptyEmpData();
        this.beginDateScope = '';
        this.loadEmps();
      },
      showAdvanceSearchView(){
        this.advanceSearchViewVisible = !this.advanceSearchViewVisible;
        this.keywords = '';
        if (!this.advanceSearchViewVisible) {
          this.emptyEmpData();
          this.beginDateScope = '';
          this.loadEmps();
        }
      },
      handleSelectionChange(val) {
        this.multipleSelection = val;
      },
      deleteManyEmps(){
        this.$confirm('此操作将删除[' + this.multipleSelection.length + ']条数据, 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          var ids = '';
          for (var i = 0; i < this.multipleSelection.length; i++) {
            ids += this.multipleSelection[i].id + ",";
          }
          this.doDelete(ids);
        }).catch(() => {
        });
      },
      deleteEmp(row){
        this.$confirm('此操作将永久删除[' + row.name + '], 是否继续?', '提示', {
          confirmButtonText: '确定',
          cancelButtonText: '取消',
          type: 'warning'
        }).then(() => {
          this.doDelete(row.id);
        }).catch(() => {
        });
      },
      doDelete(ids){
        this.tableLoading = true;
        var _this = this;
        this.deleteRequest("/student/mana/deleteStudentById/" + ids).then(resp=> {
          _this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.$message({type: data.status, message: data.msg});
            _this.loadEmps();
          }
        })
      },

      keywordsChange(val){
        if (val == '') {
          this.loadEmps();
        }
      },
      searchEmp(){
        this.loadEmps();
      },
      currentChange(currentChange){
        this.currentPage = currentChange;
        this.loadEmps();
      },
      loadEmps(){
        var _this = this;
        this.tableLoading = true;
        this.getRequest("/student/mana/getAllStudents?page=" + this.currentPage + "&size=10&keywords=" + this.keywords).then(resp=> {
          this.tableLoading = false;
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.students = data.students;
            _this.totalCount = data.count;
//            _this.emptyEmpData();
          }
        })
      },
      addformula(addFormulaForm){
        var _this = this;
        this.$refs[addFormulaForm].validate((valid) => {
          if (valid) {
              this.tableLoading = true;
              this.putRequest("/student/mana/updateStudent", this.formulas).then(resp=> {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  _this.$message({type: data.status, message: data.msg});
                  _this.dialogFormulaVisible = false;
                  _this.loadEmps();
                }
              })
          } else {
            return false;
          }
        });
      },
      addEmp(formName){
        var _this = this;
        this.$refs[formName].validate((valid) => {
          if (valid) {
              if(this.student.among_them == ""){
                this.student.among_them = "0";
              }
              if(this.student.final_exam== ""){
                this.student.final_exam = "0";
              }
            if (this.student.id) {
              //更新
              this.student.flag=2;
              this.tableLoading = true;
              console.info(this.student);
              this.putRequest("/student/mana/updateStudent", this.student).then(resp=> {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  _this.$message({type: data.status, message: data.msg});
                  _this.dialogVisible = false;
                  _this.emptyEmpData();
                  _this.loadEmps();
                }
              })
            } else {
              //添加
              this.tableLoading = true;
              this.postRequest("/student/mana/addStudent", this.student).then(resp=> {
                _this.tableLoading = false;
                if (resp && resp.status == 200) {
                  var data = resp.data;
                  _this.$message({type: data.status, message: data.msg});
                  _this.dialogVisible = false;
                  _this.emptyEmpData();
                  _this.loadEmps();
                }
              })
            }
          } else {
            return false;
          }
        });
      },
      cancelEidt(){
        this.dialogVisible = false;
        this.emptyEmpData();
      },
      cancelFormulaEidt(){
        this.dialogFormulaVisible = false;
        this.emptyFormula();
      },

      showDepTree(){
        this.showOrHidePop = !this.showOrHidePop;
      },
      showDepTree2(){
        this.showOrHidePop2 = !this.showOrHidePop2;
      },
      handleNodeClick(data) {
        this.student.departmentName = data.name;
        this.student.departmentId = data.id;
        this.showOrHidePop = false;
        this.depTextColor = '#606266';
      },
      handleNodeClick2(data) {
        this.student.departmentName = data.name;
        this.student.departmentId = data.id;
        this.showOrHidePop2 = false;
        this.depTextColor = '#606266';
      },
      initData(){
        var _this = this;
        this.getRequest("/student/mana/getAllClass").then(resp=> {
          if (resp && resp.status == 200) {
            var data = resp.data;
            _this.studentClass = data.class;
            console.info(_this.studentClass);
          }
        })
      },
      showOrd(row){
        this.dialogOrdTitle = "添加平时成绩";
        this.dialogOrdVisible = true;
      },
      showEditEmpView(row){
        this.dialogTitle = "编辑学生";
        this.student = row;
        this.dialogVisible = true;
      },
      showAddEmpView(){
        this.dialogTitle = "添加学生";
        this.dialogVisible = true;
        var _this = this;
      },
      showAddFormulaView(){
        if(this.students.length>0){
          this.dialogFormulaTitle = "添加计算公式";
          this.dialogFormulaVisible = true;
          if(this.students[0].formula != "未添加"){
            this.formulas.formula=this.students[0].formula;
          }
        }else{
          var _this = this;
          _this.$message({type: "error", message: "请添加学生信息"});
        }
      },
      emptyFormula(){
        this.formulas= {
          formula:'',
          flag:1
        }
      },
      emptyEmpData(){
        this.student = {
          name: '',
          studentId:'',
          gender: '',
          studentClassId: '',
          among_them:'',
          final_exam:'',
          flag:2,
          create_by:JSON.parse(window.localStorage.getItem("user")).id,
          update_by:JSON.parse(window.localStorage.getItem("user")).id
        }
      }
    }
  };
</script>
<style>
  .el-dialog__body {
    padding-top: 0px;
    padding-bottom: 0px;
  }

  .slide-fade-enter-active {
    transition: all .8s ease;
  }

  .slide-fade-leave-active {
    transition: all .8s cubic-bezier(1.0, 0.5, 0.8, 1.0);
  }

  .slide-fade-enter, .slide-fade-leave-to {
    transform: translateX(10px);
    opacity: 0;
  }
</style>



